import Link from "next/link"
import { Navbar } from "@/components/navbar"
import { Footer } from "@/components/footer"
import { Card, CardContent } from "@/components/ui/card"
import { Button } from "@/components/ui/button"

export default function Home() {
  return (
    <div className="min-h-screen flex flex-col bg-background text-foreground selection:bg-primary/30">
      <Navbar />
      <main className="flex-1 relative overflow-hidden">
        {/* Background Gradients */}
        <div className="absolute top-0 left-1/2 -translate-x-1/2 w-full h-[500px] bg-primary/20 blur-[120px] rounded-full pointer-events-none" />
        <div className="absolute bottom-0 right-0 w-[500px] h-[500px] bg-accent/10 blur-[120px] rounded-full pointer-events-none" />

        {/* Hero Section */}
        <section className="relative pt-32 pb-20 px-4 flex items-center justify-center">
          <div className="max-w-5xl mx-auto text-center z-10">
            <div className="inline-flex items-center rounded-full border border-primary/30 bg-primary/10 px-3 py-1 text-sm text-primary mb-8 backdrop-blur-sm">
              <span className="flex h-2 w-2 rounded-full bg-primary mr-2 animate-pulse"></span>
              v2.0 Now Live for Universities
            </div>

            <h1 className="text-5xl md:text-7xl font-bold tracking-tight mb-8 leading-tight">
              The Future of Campus <br />
              <span className="text-transparent bg-clip-text bg-gradient-to-r from-white via-primary to-accent">
                Attendance is Here.
              </span>
            </h1>

            <p className="text-xl text-muted-foreground mb-4 font-medium max-w-2xl mx-auto">
              AI-driven tracking for forward-thinking universities. Experience the precision of next-generation academic biometric tools.
            </p>

            <div className="flex flex-col sm:flex-row gap-4 justify-center mt-10">
              <Button size="lg" className="rounded-full text-lg h-12 px-8 shadow-lg shadow-primary/25" asChild>
                <Link href="/auth/signup">Get Started Today</Link>
              </Button>
              <Button size="lg" variant="outline" className="rounded-full text-lg h-12 px-8 border-white/10 bg-white/5 hover:bg-white/10 backdrop-blur-sm" asChild>
                <Link href="/contact">Watch Preview</Link>
              </Button>
            </div>

            {/* Stats */}
            <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mt-20 max-w-4xl mx-auto text-left">
              <Card className="bg-white/5 border-white/10 backdrop-blur-md">
                <CardContent className="p-6">
                  <div className="text-sm text-muted-foreground mb-1">Attendance Accuracy</div>
                  <div className="text-3xl font-bold text-foreground">99.9% <span className="text-xs text-green-400 align-top">+2.4%</span></div>
                </CardContent>
              </Card>
              <Card className="bg-white/5 border-white/10 backdrop-blur-md">
                <CardContent className="p-6">
                  <div className="text-sm text-muted-foreground mb-1">Students Enrolled</div>
                  <div className="text-3xl font-bold text-foreground">500k+ <span className="text-xs text-green-400 align-top">+15%</span></div>
                </CardContent>
              </Card>
              <Card className="bg-white/5 border-white/10 backdrop-blur-md">
                <CardContent className="p-6">
                  <div className="text-sm text-muted-foreground mb-1">Global Institutions</div>
                  <div className="text-3xl font-bold text-foreground">150+ <span className="text-xs text-green-400 align-top">+8%</span></div>
                </CardContent>
              </Card>
            </div>
          </div>
        </section>

        {/* Features Section */}
        <section className="py-24 px-4 relative">
          <div className="max-w-6xl mx-auto">
            <h2 className="text-4xl font-bold text-foreground text-center mb-4">State-of-the-Art Attendance</h2>
            <p className="text-muted-foreground text-center max-w-2xl mx-auto mb-16">
              Our futuristic theme powers a suite of advanced biometric and analytical tools designed specifically for digital-first universities.
            </p>

            <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
              {[
                {
                  title: "Face-ID Integration",
                  desc: "Biometric precision using next-gen facial recognition algorithms for effortless check-ins.",
                  icon: "ðŸ‘¤",
                  color: "bg-blue-500/20 text-blue-400"
                },
                {
                  title: "Geo-Fencing",
                  desc: "Ensure students are physically in the classroom with high-accuracy spatial tracking.",
                  icon: "ðŸ“",
                  color: "bg-purple-500/20 text-purple-400"
                },
                {
                  title: "Insightful Analytics",
                  desc: "Data-driven trends and real-time reporting for institutional success and transparency.",
                  icon: "ðŸ“Š",
                  color: "bg-cyan-500/20 text-cyan-400"
                },
              ].map((feature, idx) => (
                <div key={idx} className="group relative">
                  <div className="absolute inset-0 bg-gradient-to-b from-primary/10 to-transparent opacity-0 group-hover:opacity-100 transition-opacity duration-500 rounded-2xl" />
                  <Card className="h-full bg-white/5 border-white/10 backdrop-blur-sm overflow-hidden hover:border-primary/50 transition-colors duration-300">
                    <CardContent className="p-8">
                      <div className={`w-16 h-16 rounded-2xl ${feature.color} flex items-center justify-center text-3xl mb-6 shadow-inner`}>
                        {feature.icon}
                      </div>
                      <h3 className="text-xl font-bold text-foreground mb-3">{feature.title}</h3>
                      <p className="text-muted-foreground leading-relaxed">
                        {feature.desc}
                      </p>
                    </CardContent>
                  </Card>
                </div>
              ))}
            </div>
          </div>
        </section>

        {/* Seamless Workflow / Upgrade Section */}
        <section className="py-20 px-4">
          <div className="max-w-5xl mx-auto">
            <div className="rounded-3xl bg-gradient-to-r from-primary to-accent p-12 md:p-20 text-center relative overflow-hidden">
              <div className="absolute inset-0 bg-[url('/grid-pattern.svg')] opacity-20"></div>
              <div className="relative z-10">
                <h2 className="text-3xl md:text-5xl font-bold text-white mb-6">Upgrade Your Institution Today</h2>
                <p className="text-white/80 text-lg mb-10 max-w-2xl mx-auto">
                  Join over 150+ global universities in redefining campus life. Effortless tracking, powerful insights, and futuristic design.
                </p>
                <div className="flex flex-col sm:flex-row gap-4 justify-center">
                  <Button size="lg" className="bg-white text-primary hover:bg-white/90 rounded-full h-12 px-8 font-semibold">
                    <Link href="/auth/signup">Request Access</Link>
                  </Button>
                  <Button size="lg" variant="outline" className="text-white border-white/20 bg-white/10 hover:bg-white/20 rounded-full h-12 px-8 backdrop-blur-md">
                    <Link href="/contact">Speak to Sales</Link>
                  </Button>
                </div>
              </div>
            </div>
          </div>
        </section>
      </main>
      <Footer />
    </div>
  )
}
